/* @import url('https://fonts.googleapis.com/css?family=Athiti'); */

body {
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  /* font-family: 'Athiti' !important; */
}


body {
  padding: 0;
  margin: 0;
}

.wrapper-option-main {
  position: relative;
  min-height: 100vh;
}

.wrapper-option-main .d-flex {
  display: flex;
}

.wrapper-option-main .align-items-center {
  align-items: center;
}

.wrapper-option-main .justify-content-between {
  justify-content: space-between;
}

.wrapper-option-main div,
.wrapper-option-main li {
  font-weight: 400;
  font-size: 16px;
  /* color: #ffff; */
}

.wrapper-option-main .wrapper-emote {
  /* width: 400px; */
  position: absolute;
  right: 10px;
  top: calc(50vh - 350px);
  background: rgb(0 0 0 / 60%);
  padding: 25px 15px 20px;
  border-radius: 12px;
  max-height: 700px;
}

.wrapper-option-main .wrapper-emote-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}

.wrapper-option-main .wrapper-emote-title-inner {
  font-size: 26px;
}

.wrapper-option-main .btn-action-hotkey {
  /* background-color: #5e5e5e; */
  /* padding: 10px; */
  /* border-radius: 50%; */
  display: flex;
  cursor: pointer;
}

.wrapper-option-main .wrapper-filter-search {
  max-width: 100%;
}

.wrapper-option-main .wrapper-filter-search input {
  width: 100%;
  max-width: 100%;
  padding: 6px 15px;
  border: 0;
  background-color: #5e5e5e;
  border-radius: 25px;
  color: #ccc;
  height: 40px;
  font-size: 18px;
}

.wrapper-option-main .wrapper-filter-search input:focus-visible {
  outline: none !important;
}

.wrapper-option-main .wrapper-filter-search ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 15px 0 20px;
}

.wrapper-option-main .wrapper-filter-search ul li {
  background-color: #5e5e5e;
  padding: 10px;
  border-radius: 50%;
  display: flex;
  cursor: pointer;
}

.wrapper-option-main .wrapper-filter-search ul li img {
  opacity: 0.5;
}
/* 
.wrapper-option-main .wrapper-filter-search ul li:hover,
.wrapper-option-main .wrapper-filter-search ul li:hover img,
.wrapper-option-main .wrapper-filter-search ul li.active-filter,
.wrapper-option-main .wrapper-filter-search ul li.active-filter img {
  background-color: #16a0eb;
  opacity: 1;
} */

.wrapper-option-main .wrapper-emote-content-title {
  margin-bottom: 10px;
}

.wrapper-option-main .wrapper-emote-content-title .wrapper-emote-content-title-inner {
  font-size: 18px;
}

.wrapper-option-main .wrapper-emote-content-inner {
  cursor: pointer;
  padding: 8px 12px;
  font-size: 16px;
  /* background-color: #272727; */
  border-radius: 12px;
  margin-bottom: 10px;
}

/* .wrapper-option-main .wrapper-emote-content-inner.active-emote,
.wrapper-option-main .wrapper-emote-content-inner.active-hotkey,
.wrapper-option-main .wrapper-emote-content-inner:hover {
  background-color: #16a0eb;
} */

.wrapper-emote-content-icon {
  font-size: 0;
}

.wrapper-option-main .wrapper-emote-content-icon img {
  opacity: 0.5;
}

.wrapper-option-main .wrapper-emote-content-icon.active-img img {
  opacity: 1;
}

.margin-section {
  margin-bottom: 20px;
}

.margin-section .emotes-list {
  max-height: 450px;
  overflow-y: scroll;
}

.emotes-list::-webkit-scrollbar {
  display: none;
  /* for Chrome, Safari, and Opera */
}

.history-list {
  max-height: 150px;
  overflow-y: scroll;
}

.history-list::-webkit-scrollbar {
  display: none;
  /* for Chrome, Safari, and Opera */
}

.wrapper-option-main .wrapper-emote-footer {
  position: absolute;
  bottom: -40px;
  background: rgb(0 0 0 / 60%);
  width: 100%;
  left: 0;
  padding: 4px;
  border-radius: 12px;
}

.wrapper-option-main .wrapper-emote.wrapper-hotkey {
  right: 430px;
  top: calc(50vh - 290px);
}

.wrapper-option-main .wrapper-emote.wrapper-hotkey .wrapper-emote-content-hotkey {
  min-width: 200px;
}

.emotes-icon {
  margin-right: 3px;
}

.text-red {
  color: rgb(255, 132, 132);
}

.text-primary {
  color: rgb(126, 186, 255);
}

.tool-tips {
  position: fixed;
  left: 50%;
  bottom: 5px;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  background: rgb(0 0 0 / 60%);
  padding: 3px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 10px;
}

.input-panel {
  display: flex;
  flex-direction: row;
}

.button-clear {
  position: absolute;
  right: 25px;
  padding-top: 8px;
}

/* Toggle A */
input:checked ~ .dot {
  transform: translateX(100%);
  background-color: rgb(126, 186, 255);
}

.label-red {
  color: red;
}